import React, { Component } from "react";
import Header from "../../components/header/Header";
import GithubRepoCard from "../../components/githubRepoCard/GithubRepoCard";
import TopButton from "../../components/topButton/TopButton";
import { Fade } from "react-reveal";
import { projectsHeader } from "../../portfolio.js";
import "./Projects.css";
import { fetchWithApiKey } from "./ProjectManager.js";
import { ShimmerThumbnail } from "react-shimmer-effects";

class Projects extends Component {
  constructor(props) {
    super(props);
    this.state = {
      projects: [],
      loading: true, // üëà loading flag
      showToast: false,
      toastMessage: "",
    };
    this.toastTimeout = null;
  }

  componentWillUnmount() {
    if (this.toastTimeout) {
      clearTimeout(this.toastTimeout);
    }
  }

  showToast = (message) => {
    this.setState({ showToast: true, toastMessage: message });

    if (this.toastTimeout) {
      clearTimeout(this.toastTimeout);
    }

    this.toastTimeout = setTimeout(() => {
      this.setState({ showToast: false });
    }, 1500);
  };

  async componentDidMount() {
    try {
      const projectList = await fetchWithApiKey();
      this.setState({ projects: projectList, loading: false });
    } catch (error) {
      console.error("Error fetching projects:", error);
      this.setState({ loading: false });
    }
  }

  render() {
    const theme = this.props.theme;
    const { projects, loading, showToast, toastMessage } = this.state;

    return (
      <div className="projects-main">
        <Header theme={theme} />
        <div className="basic-projects">
          <Fade bottom duration={2000} distance="40px">
            <div className="projects-heading-div">
              <div className="projects-heading-text-div">
                <h1
                  className="projects-heading-text"
                  style={{ color: theme.text }}
                >
                  {projectsHeader.title}
                </h1>
              </div>
            </div>
          </Fade>
        </div>

        {loading ? (
          <div className="shimmer-grid">
            {Array.from({ length: 8 }).map((_, i) => (
              <ShimmerThumbnail rounded className="shimmer" />
            ))}
          </div>
        ) : (
          <div className="repo-cards-div-main">
            {projects.map((repo, index) => (
              <GithubRepoCard
                key={`repo-${index}`}
                repo={repo}
                theme={theme}
                showToast={this.showToast}
              />
            ))}
          </div>
        )}

        {showToast && <div className="projects-toast">{toastMessage}</div>}
        <TopButton theme={this.props.theme} />
      </div>
    );
  }
}

export default Projects;
