import { collection, getDocs } from "firebase/firestore";
import axios from "axios";
import { initializeApp, getApps } from "firebase/app";
import { getFirestore } from "firebase/firestore";

export const fetchWithApiKey = async () => {
  try {
    const response = await axios.get(process.env.REACT_APP_BASEURL, {
      headers: {
        "x-api-key": process.env.REACt_APP_X_API_TOKEN,
      },
    });

    // console.log(response);

    if (response.status === 200) {
      const firebaseConfig = {
        apiKey: response.data.apiKey,
        authDomain: response.data.authDomain,
        projectId: response.data.projectId,
        storageBucket: response.data.storageBucket,
        messagingSenderId: response.data.messagingSenderId,
        appId: response.data.appId,
        measurementId: response.data.measurementId,
      };

      // Initialize Firebase only if not already initialized
      const app =
        getApps().length === 0 ? initializeApp(firebaseConfig) : getApps()[0];
      const db = getFirestore(app);

      const querySnapshot = await getDocs(collection(db, "Projects"));
      const data = querySnapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      }));

      const sortedData = data.sort((a, b) => a.sequence - b.sequence);

      console.log("Fetched and sorted Firestore data:", sortedData);
      return sortedData;
    } else {
      console.warn("Non-200 response received:", response.status);
      return [];
    }
  } catch (error) {
    console.error("API call or Firestore fetch failed:", error);
    return [];
  }
};
