import React from "react";
import "./GithubRepoCard.css";
import { Fade } from "react-reveal";
import toast from "react-hot-toast";

export default function GithubRepoCard({ repo, theme }) {
  const isValidUrl = (url) => url && url.trim() !== "";

  const handleCopy = async (url) => {
    // Early return if no URL
    if (!url) {
      toast.error("No link to copy!");
      return;
    }
    const textarea = document.createElement("textarea");
    try {
      // Create a hidden textarea as a fallback

      textarea.value = url;
      textarea.style.position = "fixed";
      textarea.style.opacity = "0";
      document.body.appendChild(textarea);

      // Try modern API first
      if (navigator.clipboard) {
        await navigator.clipboard.writeText(url);
      }
      // Fallback for older browsers
      else {
        textarea.select();
        document.execCommand("copy");
      }

      toast.success("Link copied!");
    } catch (err) {
      console.error("Copy failed:", err);
      toast.error("Failed to copy link");
    } finally {
      // Clean up
      if (textarea && textarea.parentNode) {
        textarea.parentNode.removeChild(textarea);
      }
    }
  };

  // Reusable function for rendering store/web copy links
  const renderLinkItem = (url, iconClass, bgColor, ariaLabel) => {
    if (!isValidUrl(url)) return null;
    return (
      <li style={{ "--bg": bgColor }}>
        <a
          href={url}
          target="_blank"
          rel="noopener noreferrer"
          aria-label={ariaLabel}
        >
          <i className={iconClass + " store-icon"}></i>
        </a>
        <button
          className="copy-btn-mobile"
          style={{ fontSize: 10, fontWeight: "bold" }}
          onClick={() => handleCopy(url)}
          aria-label={`Copy ${ariaLabel}`}
        >
          <i
            className="fa-solid fa-link link-icon"
            style={{ fontSize: 10 }}
          ></i>
          Copy link
        </button>
      </li>
    );
  };

  return (
    <div className="repo-card-div" style={{ backgroundColor: theme.whiteBG }}>
      <Fade bottom duration={2000} distance="40px">
        <div key={repo?.id}>
          <div className="repo-name-div">
            {isValidUrl(repo?.project_logo) ? (
              <img
                src={repo?.project_logo || ""}
                alt="App Logo"
                className="app-logo"
              />
            ) : (
              <svg
                aria-hidden="true"
                className="octicon repo-svg"
                height="16"
                role="img"
                viewBox="0 0 12 16"
                width="12"
              >
                <path
                  fillRule="evenodd"
                  d="M4 9H3V8h1v1zm0-3H3v1h1V6zm0-2H3v1h1V4zm0-2H3v1h1V2zm8-1v12c0 
                  .55-.45 1-1 1H6v2l-1.5-1.5L3 
                  16v-2H1c-.55 0-1-.45-1-1V1c0-.55.45-1 
                  1-1h10c.55 0 1 .45 1 1zm-1 
                  10H1v2h2v-1h3v1h5v-2zm0-10H2v9h9V1z"
                ></path>
              </svg>
            )}
            <p className="repo-name" style={{ color: theme.text }}>
              {repo?.project_name || ""}
            </p>
          </div>

          <div className="repo-chip">
            {repo?.project_tags?.map((tag, idx) => (
              <span className="chip" key={idx}>
                {tag}
              </span>
            ))}
          </div>

          <div className="repo-stats">
            <ul className="social">
              {renderLinkItem(
                repo?.appstore_url,
                "fa-brands fa-apple",
                "#1D9BF0",
                "App Store link"
              )}
              {renderLinkItem(
                repo?.playstore_url,
                "fa-brands fa-google-play",
                "#3DDC84",
                "Play Store link"
              )}
              {renderLinkItem(
                repo?.web_url,
                "fa-solid fa-globe",
                "#4285F4",
                "Web link"
              )}
            </ul>
          </div>
        </div>
      </Fade>
    </div>
  );
}
